import { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
// import Spinner from '../spinner/spinner';
// import ErrorMessage from '../errorMessage/ErrorMessage';
import useCryptoService from '../../services/cryptoService';  

import classes from '../../styles/app.module.css';
import CryptoDescr from '../cryptoDescr/CryptoDescr';
import CryptoData from '../cryptoData/CryptoData';
import CryptoMarketsTable from '../cryptoMarketsTable/CryptoMarketsTable';
import Footer from '../footer/Footer';
import TradingViewChart from '../tradingViewChart/TradingViewChart';
import Header from '../header/Header';


const CryptoCurrencyPage = (props) => {

    const selectedCrypto = props.selectedCrypto;

    const { getCrypto } = useCryptoService();

    const { cryptoId } = useParams();
    const [crypto, setCrypto] = useState({});

    const onChangeCryptoData = (data) => {
        setCrypto(data);
    }
    
    const updateCrypto = () => {
        getCrypto(cryptoId)
        .then(onCryptoLoaded)
    }

    useEffect(() => {
        updateCrypto();
        // eslint-disable-next-line
    }, [cryptoId])
    
    const onCryptoLoaded = (cryptoId) => {
        setCrypto(cryptoId);
    }

    // const errorMessage = error ? <ErrorMessage/> : null;
    // const spinner = loading ? <Spinner className={classes.spinner}/> : null;
    // const content = !(loading || error || !crypto) ? <View 
    const content = !(!crypto) ? <View 
                                    crypto={crypto} 
                                    onChangeCryptoData={onChangeCryptoData} 
                                    selectedCrypto={selectedCrypto}/> 
                                : null;


    return (
        <>
            {/* {errorMessage}
            {spinner} */}
            {content}
        </>
        
    );
};

const View = (props) => {
    const {crypto, onChangeCryptoData} = props;
    const data = [crypto.name, crypto.symbol];

    return (
        <>
            <Header/>
            <div className={classes.crypto_info}>
                {/* <button 
                    className={`${classes.btn_back} ${classes.btn}`}><Link to='/'>Back to main page</Link></button> */}
                {/* <Link to='/'>Back to main page</Link> */}
                
                <CryptoDescr crypto={crypto}/>             
            
                <CryptoData crypto={crypto}/>
                            
                <TradingViewChart data={data} />            
                        
                <CryptoMarketsTable 
                    crypto={crypto} 
                    onChangeCryptoData={onChangeCryptoData} />

                <Footer style={{width: 'calc(100% - 80px)', boxSizing: 'initial'}}/>

                <button 
                    onClick={() => window.scrollTo(0, 0)}
                    className={`${classes.btn_top} ${classes.btn}`}>To top</button>

            </div>
        </>
        
    )
}

export default CryptoCurrencyPage;