import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { useHttp } from '../hooks/http.hook';

const initialState = {
    portfolioLoading: 'idle',
    amountError: false,
    displaySearchForm: false,
    displayAddForm: false,
    selectedCrypto: {},
    cryptoList: [],
    portfolioPrices: []
}

export const fetchPortfolioPrices = createAsyncThunk(
    'portfolio/fetchPortfolioPrices',
    async (query) => {
        if (query) {
            const { request } = useHttp();
            return await request(`https://api.coingecko.com/api/v3/simple/price?ids=${query}&vs_currencies=usd`, 
                'GET', null, {
                    'accept': 'application/json',
                }
            );
        }
    }
)

export const fetchCryptoById = createAsyncThunk(
    'portfolio/fetchCryptoById',
    async (id) => {
        const { request } = useHttp();
        const result = await request(`https://api.coingecko.com/api/v3/coins/${id}?community_data=false&developer_data=false&sparkline=false`, 
        'GET', null, {
            'accept': 'application/json',
        }
    );
    const _transformCrypto = (crypto) => {
        return {
            id: crypto.id,
            symbol: crypto.symbol,
            descr: crypto.description.en,
            price: crypto.market_data.current_price.usd,
            current_price: crypto.market_data.current_price.usd,
            ath_price: crypto.market_data.ath.usd,
            ath_date: crypto.market_data.ath_date.usd,
            price_change_percentage_24h: crypto.market_data.price_change_percentage_24h,
            name: crypto.name,
            image: crypto.image,
            links: crypto.links.homepage,
            market_cap: crypto.market_data.market_cap.usd,
            market_cap_rank: crypto.market_cap_rank,
            markets: crypto.tickers.map(market => [
                market.market.name, 
                market.target,
                market.last,
                market.volume,
                market.trade_url
            ])
        }
    }
    const res = _transformCrypto(result);
    return res;
    }
)

const portfolioSlice = createSlice({
    name: 'portfolio',
    initialState,
    reducers: {
        showSearchForm: state => {state.displaySearchForm = true},
        hideSearchForm: state => {state.displaySearchForm = false},
        showAddForm: state => {state.displayAddForm = true},
        hideAddForm: state => {state.displayAddForm = false},
        selectCrypto: (state, action) => {state.selectedCrypto = action.payload},
        addCrypto: (state, action) => {state.cryptoList.push(action.payload)},
        modifyCrypto: (state, action) => {
            const item = state.cryptoList.find(item => item.name === action.payload.name)
            if (item) {
                if (action.payload.type === 'buy') {
                    item.price = (+item.price + +action.payload.price) / 2;
                    item.amount = +item.amount + +action.payload.amount;
                    item.total = +item.total + +action.payload.total;
                    state.displayAddForm = false;
                }
                if (action.payload.type === 'sell') {
                    if (+item.amount - +action.payload.amount >= 0) {
                        item.amount = +item.amount - +action.payload.amount;
                        item.total = +item.total - +action.payload.total;
                        state.displayAddForm = false;
                    } else {
                        state.amountError = true;
                    }
                }
            } else {
                state.cryptoList.push(action.payload)
            }
            localStorage.setItem('cryptoAssets', JSON.stringify(state.cryptoList));
        },
        checkSavedAssets: (state) => {
            if (state.cryptoList.length === 0 && localStorage.cryptoAssets) {
                state.cryptoList = JSON.parse(localStorage.cryptoAssets);
            }
        },
        deleteCryptoAsset: (state, action) => {
            state.cryptoList = state.cryptoList.filter(item => item.id !== action.payload);
            localStorage.setItem('cryptoAssets', JSON.stringify(state.cryptoList))
        }
    },
    extraReducers: (builder) => {
        builder
            .addCase(fetchPortfolioPrices.pending, state => {state.portfolioLoading = 'loading'})
            .addCase(fetchPortfolioPrices.fulfilled, (state, action) => {
                const priceList = Object.entries(action.payload).map(i => {
                    const obj = {};
                    obj['id'] = i[0];
                    obj['usd'] = i[1].usd;
                    return obj;
                })
                state.portfolioLoading = 'idle';
                state.portfolioPrices = [];
                state.portfolioPrices.push(priceList)
                state.portfolioPrices = state.portfolioPrices.flat();
            })
            .addCase(fetchPortfolioPrices.rejected, state => {state.portfolioLoading = 'error'})
            .addCase(fetchCryptoById.pending, state => {state.portfolioLoading = 'loading'})
            .addCase(fetchCryptoById.fulfilled, (state, action) => {state.selectedCrypto = action.payload})
            .addCase(fetchCryptoById.rejected, state => {state.portfolioLoading = 'error'})
            .addDefaultCase(() => {})
    }
})

const {actions, reducer} = portfolioSlice;

export default reducer;

export const {
    showSearchForm,
    hideSearchForm,
    showAddForm,
    hideAddForm,
    selectCrypto,
    addCrypto,
    modifyCrypto,
    checkSavedAssets,
    deleteCryptoAsset
} = actions