import {Link} from 'react-router-dom';
import {v4 as uuidv4} from 'uuid';
import classes from '../../styles/app.module.css';

const ListItem = (props) => {
    const {coin, i, coinPriceChange} = props;

    const showSpaces = (numb) => {
        return [...(numb + '')].reverse().join('').replace(/([0-9]{3})/g, '$1 ').split('').reverse().join('');
    } 

    const changeStyle = (price) => {
        if (price > 0) {
            return classes.price_pos;
        } else {
            return classes.price_neg;
        }
    }

    return (
        <Link 
            to={`/${coin.id}`}
            key={uuidv4()} 
            onClick={() => props.onSelectCrypto(coin.id)}
            className={classes.coin}>
            <div className={classes.coin_order}>
                <span className={classes.coin_order__num}>{i + 1}</span>
            </div>
            <div className={classes.coin_wrapper}>
                <div className={classes.coin_header}>
                    <h3>{coin.name}</h3>
                    <div className={classes.coin_img__wrapper}><img src={coin.image} alt={coin.name} /></div>
                    <span className={changeStyle(coinPriceChange)}>{coinPriceChange > 0 ? '+' : ''}{coinPriceChange?.toFixed(4)} %</span>
                </div>
                <div className={classes.coin_body}>
                    <div className={classes.coin_price}>
                        <h4>Price:</h4>
                        <p>{coin.current_price} $</p>
                    </div>
                    <div className={classes.coin_cap}>
                        <h4>Market cap:</h4>
                        <p>{showSpaces(coin.market_cap)} $</p>
                    </div>
                </div>
            </div>
        </Link>
    );
};

export default ListItem;