import { useContext, useState, useEffect } from "react";
import {v4 as uuidv4} from 'uuid';

import Spinner from '../spinner/spinner';
import ErrorMessage from '../errorMessage/ErrorMessage';
import useCryptoService from "../../services/cryptoService";
import context from "../context/context";
import Sort from "../sort/Sort";
import Search from "../search/Search";
import ListItem from "../listItem/ListItem";
import SearchItem from "../searchItem/SearchItem";

import classes from '../../styles/app.module.css';


const Cryptolist = (props) => {
    const { loading, error } = useCryptoService();
    const {data} = useContext(context);
    const [list, setList] = useState([]);
    const [isSearch, setIsSearch] = useState(false);
    const [searchList, setSearchList] = useState([]);

    const {amount, onChangeAmount} = props;

    useEffect(() => {
        setList(data);
    }, [data])

    const setSearchQuery = (query) => {
        setList(query);
    }

    const onManualSearch = (data) => {
        setIsSearch(true);
        setSearchList(data);
    }
    
    const coins = list.map((coin, i) => {
        const coinPriceChange = coin.price_change_percentage_24h;
        return (
            <ListItem 
                key={uuidv4()}
                coin={coin} 
                i={i}
                onSelectCrypto={props.onSelectCrypto}
                coinPriceChange={coinPriceChange}
            />
        )
    })

    const searchCoins = searchList?.map((coin, i) => {
        return (
            <SearchItem
                key={uuidv4()}
                coin={coin}
                i={i}
                onSelectCrypto={props.onSelectCrypto}
                isMainPage={true}
            />
        )
    })

    const errorMessage = error ? <ErrorMessage/> : null;
    const spinner = loading ? <Spinner/> : null;
    const content = !(errorMessage || spinner) 
            ? !isSearch 
                ? coins.length === 0 
                                ? <h3 style={{textAlign: 'center', margin: '20px auto', maxWidth: 500}}>Sorry, 
                                    there is no coins in top 250 with entered name. Please try again or use search button to find it</h3> 
                                : coins
                : searchCoins?.length !== 0 
                                ? searchCoins 
                                : <h3 style={{textAlign: 'center', margin: '20px auto', maxWidth: 500}}>Sorry, 
                                        there is no coins with entered name. Please try again</h3>
            : null;

    return (
        <div className={classes.list_wrapper} style={props.style}>
            <h2 className={classes.header}>Cryptolist</h2>
            <h3 className={classes.sort_header} style={{marginBottom: '10px'}}>Sort crypto</h3>
            <Sort/>
            <Search 
                isMainPage={true}
                setSearchQuery={setSearchQuery} 
                amount={amount} 
                onManualSearch={onManualSearch}
                onChangeAmount={onChangeAmount}/>
            {errorMessage}
            {spinner}
            {content}
            {/* {coins} */}
            <div className={classes.paginator_wrapper}>
                <button 
                    onClick={() => props.onChangeAmount(amount => amount === 10 ? amount + 90 : amount + 100)} 
                    className={`${classes.btn} ${amount >= 250 || isSearch ? classes.hide : ''}`} 
                    >Load more</button>
                <button 
                    onClick={() => {
                        setIsSearch(false);
                        props.onSelectCrypto(null)
                    }} 
                    className={`${classes.btn} ${!isSearch ? classes.hide : ''}`} 
                    >Return to top 10 coinlist</button>
            </div>
        </div>
    );
};

export default Cryptolist;