import { useState, useEffect } from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {v4 as uuidv4} from 'uuid';

import Header from "../header/Header";
import cryptoService from '../../services/cryptoService';
import PortfolioList from '../portfolioList/PortfolioList'
import PortfolioItemAddForm from '../portfolioItemAddForm/PortfolioItemAddForm';
import PortfolioSearchForm from '../portfolioSearchForm/PortfolioSearchForm';
import UserModal from '../userModal/UserModal';
import { showAddForm, showSearchForm, hideSearchForm, selectCrypto, fetchPortfolioPrices, checkSavedAssets } from '../../slices/portfolioSlice';
import {showMessage} from '../../slices/authSlice'

import classes from '../../styles/app.module.css';

const PortfolioPage = () => {
    const {getCurrency} = cryptoService();
    const dispatch = useDispatch();
    const {displaySearchForm, displayAddForm, cryptoList} = useSelector(state => state.portfolio);
    const {isLogged, message} = useSelector(state => state.pseudoAuth);
    const [portfolioSearchList, setPortfolioSearchList] = useState([]);

    const onSetList = (data) => {
        setPortfolioSearchList(data);
    }

    const fetchData = () => {
        if (cryptoList.length !== 0) {
            dispatch(fetchPortfolioPrices(cryptoList.map(i => i.id)))
        }
    }

    useEffect(() => {
        getCurrency(10)
            .then(data => setPortfolioSearchList(data))
            .catch(err => console.log(err))
    // eslint-disable-next-line
    }, [])

    useEffect(() => {
        dispatch(checkSavedAssets());
    // eslint-disable-next-line
    }, [])

    useEffect(() => {
        fetchData();
    // eslint-disable-next-line 
    }, [cryptoList])

    // useEffect(() => {
    //     const intervalId = setInterval(fetchData, 5000);

    //     return () => clearInterval(intervalId);
    //     // eslint-disable-next-line
    // }, [])
    
    const searchCoins = portfolioSearchList?.map((coin, i) => {
        return (
            <li 
                key={uuidv4()}
                onClick={() => {
                    dispatch(selectCrypto(coin))
                    dispatch(hideSearchForm())
                    dispatch(showAddForm())
                }}
                >{coin.name}</li>
        )
    })

    return (
        <>
            <Header />
            <UserModal />
            <section className={classes.portfolio} style={{textAlign: 'center'}}>
                <div className={classes.body_hover} style={{display: `${displayAddForm || displaySearchForm ? 'block' : 'none'}`}}></div>
                <h1>Portfolio</h1>
                <div className={classes.portfolio__container}>
                    <div className={classes.portfolio__graph_container}>
                        <span style={{
                            display: 'block', 
                            width: '100%', 
                            position: 'absolute', 
                            top: 0, 
                            left: 0,
                            color: 'rgba(0, 128, 128, 0.5)'
                            }}>This is fake graphic. It will be updated soon.</span>
                    </div>
                    
                    <PortfolioSearchForm 
                        onSetList={onSetList} 
                        searchCoins={searchCoins}
                        style={{display: `${displaySearchForm ? 'flex' : 'none'}`}}
                        />
                    
                    <PortfolioItemAddForm style={{display: `${displayAddForm ? 'flex' : 'none'}`}}/>

                    <PortfolioList/>
                    <div className={classes.portfolio__currency_add}>
                        <span>Add your cryptocurrency</span>
                        <button 
                            className={classes.btn}
                            onClick={() => {
                                if (isLogged) {
                                    dispatch(showSearchForm())
                                } else {
                                    dispatch(showMessage(true))
                                }
                                }}>+</button>
                        <p style={{color: 'red', display: `${message ? 'block' : 'none'}`}}>Please, log in!</p>
                    </div>
                </div>
            </section>
        </>
    );
};

export default PortfolioPage;