import { BrowserRouter, Routes, Route } from "react-router-dom";
import CryptoCurrencyPage from '../pages/CryptoCurrencyPage';
import MainPage from '../pages/MainPage';
import PortfolioPage from '../pages/PortfolioPage';
import PortfolioHistory from '../pages/PortfolioAssetHistoryPage';
import Page404 from "../pages/Page404";

const App = () => {

    return (
        <BrowserRouter basename='/cryptochart/'>
        {/* // <BrowserRouter> */}
            <Routes>
                <Route path="/" element={<MainPage />}/>
                <Route exact path="/:cryptoId" element={<CryptoCurrencyPage />}/>
                <Route exact path="/portfolio" element={<PortfolioPage />}/>
                <Route exact path="/portfolio/:cryptoId" element={<PortfolioHistory />}/>
                <Route path="*" element={<Page404/>}/>
            </Routes>
        </BrowserRouter>
    );
};

export default App;