import {useDispatch} from 'react-redux';

import {hideSearchForm} from '../../slices/portfolioSlice';
import Search from '../search/Search';

import classes from '../../styles/app.module.css';

const PortfolioSearchForm = (props) => {
    const dispatch = useDispatch();

    return (
        <div className={classes.portfolio__search} style={props.style}>
            <div 
                className={classes.close_modal}
                onClick={() => dispatch(hideSearchForm())}>x</div>
            <div className={classes.portfolio__search_wrapper}>
                <Search 
                    isMainPage={false}
                    onSetList={props.onSetList}/>
                <div>
                    <ul>
                        {props.searchCoins}
                    </ul>
                </div>
            </div> 
        </div>
    );
};

export default PortfolioSearchForm;