import {useRef, useEffect} from 'react';

import classes from '../../styles/app.module.css'


const TradingViewChart = (props) => {
    const selectedCrypto = props.data;
  
    const htmlScript = `
    <div class="tradingview-widget-container chart_container">
      <div id="tradingview_fa47e"></div>
      <div class="tradingview-widget-copyright"><a href="https://ru.tradingview.com/symbols/${selectedCrypto[1] === undefined ? 'btc' : selectedCrypto[1]}/" rel="noopener" target="_blank">
      <span class="blue-text">${selectedCrypto[0] === undefined ? 'none' : selectedCrypto[0]}</span></a> from TradingView</div>
      <script type="text/javascript">
      new TradingView.MediumWidget(
          {
            "symbols": [
              [
                "${selectedCrypto[0] === undefined ? 'none' : selectedCrypto[0]}",
                "${selectedCrypto[1] === undefined ? 'none' : selectedCrypto[1].toUpperCase()}${selectedCrypto[1] === 'usdt' ? 'usdc' : 'usdt'}|1D"
              ]
            ],
            "chartOnly": false,
            "width": "100%",
            "height": 500,
            "locale": "en",
            "colorTheme": "light",
            "isTransparent": false,
            "autosize": true,
            "showVolume": false,
            "hideDateRanges": false,
            "scalePosition": "right",
            "scaleMode": "Normal",
            "fontFamily": "-apple-system, BlinkMacSystemFont, Trebuchet MS, Roboto, Ubuntu, sans-serif",
            "noTimeScale": false,
            "valuesTracking": "1",
            "chartType": "line",
            "fontColor": "#787b86",
            "gridLineColor": "rgba(42, 46, 57, 0.06)",
            "container_id": "tradingview_fa47e"
        }
      );
      </script>
    </div>`
    
    const divRef = useRef();
   
    useEffect(() => {
        const fragment = document.createRange().createContextualFragment(htmlScript);
        divRef.current.children[0]?.remove();
        divRef.current.append(fragment);    
    }, [divRef, htmlScript, selectedCrypto]);
    
    useEffect(() => {
        if (document.body.querySelectorAll('style').length > 1) {
            document.body.querySelectorAll('style').forEach((item, i) => {
                if (i !== 0) {
                  item.remove();
                }
            }) 
        }
    }, [selectedCrypto])

    return (
        <div 
          ref={divRef} 
          className={classes.iframe}></div>
    );
};

export default TradingViewChart;