import { useState, useEffect } from 'react';
import {v4 as uuidv4} from 'uuid';

import classes from '../../styles/app.module.css'

const CryptoMarketsTable = (props) => {
    // eslint-disable-next-line
    const {crypto} = props;
    const [isSorted, setIsSorted] = useState(false);
    const [marketsData, setMarketsData] = useState([]);
    const [currency, setCurrency] = useState('none');
    const thead = ['Market name', 'Currency', 'Price', 'Volume', 'Link'];

    const checkPrice = (str) => {
        const parseIntNum = Number.parseInt(str);
        const parseFltNum = Number.parseFloat(str);
        if (!isNaN(parseIntNum) && parseIntNum >= 1 && (parseFltNum - parseIntNum + '').length > 4) {
            return parseFltNum.toFixed(4);
        } else if (!isNaN(parseIntNum) && (parseFltNum - parseIntNum + '').length > 4) {
            return parseFltNum.toFixed(8);
        } else {
            if (str.length > 15) {
                return `${str.slice(0, 10)}...`;
            }
            return str;
        }
    }

    useEffect(() => {
        setMarketsData(crypto.markets);
        let rotateDeg = 180;
        const rotateArr = () => {
            document.querySelector('#select_arrow').style.transform = `rotate(${rotateDeg}deg)`;
            rotateDeg += 180;
        }
        document.querySelector('#currency').addEventListener('click', rotateArr);

    // eslint-disable-next-line
    }, [])

    useEffect(() => {
        setMarketsData(currency === 'none' ? crypto.markets : crypto.markets?.filter(item => item[1] === currency));
    // eslint-disable-next-line
    }, [currency])

    const sortBy = (type) => {
        if (type === 'Market name' || type === 'Currency') {
            if (!isSorted) {
                setMarketsData(marketsData => marketsData?.sort((a, b) => a[thead.indexOf(type)].localeCompare(b[thead.indexOf(type)]))); 
                setIsSorted(isSorted => !isSorted);
            } else {
                setMarketsData(marketsData => marketsData?.sort((a, b) => b[thead.indexOf(type)].localeCompare(a[thead.indexOf(type)]))); 
                setIsSorted(isSorted => !isSorted);
            }       
        } else {
            if (!isSorted) {
                setMarketsData(marketsData => marketsData?.sort((a, b) => b[thead.indexOf(type)] - a[thead.indexOf(type)])); 
                setIsSorted(isSorted => !isSorted);
            } else {
                setMarketsData(marketsData => marketsData?.sort((a, b) => a[thead.indexOf(type)] - b[thead.indexOf(type)])); 
                setIsSorted(isSorted => !isSorted);
            }  
        }
    }

    // eslint-disable-next-line
    const theads = thead.map((item, i) => {
        if (i !== thead.length - 1) {
            return (
                <div 
                    key={uuidv4()}
                    className={classes.td}
                    onClick={() => sortBy(item)}>{item}</div>
            )
        }
    })

    const markets = marketsData?.filter(item => item[2] !== 0).filter(item => item[3] !== 0).map((market, i) => {
        return (
            <div key={uuidv4()} className={classes.tr}>
                {
                    market.map((item, i) => {
                        if (i === 0) {
                            return (
                                <div 
                                    key={uuidv4()}
                                    className={classes.td}
                                >
                                    <a href={market[4]}>{item}</a>
                                </div>
                            )
                        }
                        if (i === market.length - 1) {
                            // eslint-disable-next-line
                            return;
                        }
                        return (
                            <div 
                                className={classes.td} 
                                key={uuidv4()}>
                                    {checkPrice(item)}
                            </div>
                        )
                    }) 
                }
            </div>
        )
    })

    const options = [...new Set(crypto.markets?.map(item => item[1]))].map(item => {
        return (
            <option 
                key={uuidv4()}
                value={item}>{item}</option>
        )
    })

    return (
        <>
            <h3>Markets:</h3>
            <div className={classes.select__wrapper} style={{display: 'block', position: 'relative'}}>
                <select
                    value={currency} 
                    className={classes.fiat_select} 
                    onChange={(e) => setCurrency(e.target.value)} 
                    name="currency" 
                    id="currency">
                    <option value='none' style={currency !== 'none' ? {display: 'none'} : {display: 'block'}}>Choose currency filter</option>
                    {options}
                </select>
                <span 
                    id='select_arrow'
                    className={classes.select_arrow}></span>
            </div>
            <div style={{display: `${marketsData ? 'flex' : 'none'}`}} className={classes.table}>
                <div className={classes.thead}>
                    {theads}
                </div>
                <div className={classes.tbody}>
                    {markets}
                </div>
            </div>
        </>
    );
};

export default CryptoMarketsTable;