import classes from '../../styles/app.module.css';

const style = {marginBottom: '5px', fontSize: '16px'};

export const FiatCurrency = (props) => {
    return (
        <>
           <h3 style={style}>Please choose fiat currency</h3>
            <select 
                value={props.fiatCoin} 
                className={classes.fiat_select} 
                onChange={(e) => props.onUpdateSelect(e, 'fiat')} 
                name="fiat" 
                id="fiat">
                {props.optionsFiat}
            </select>
        </> 
    )
}
export const CryptoCurrency = (props) => {
    return (
        <>
           <h3 style={style}>Please choose crypto currency</h3>
            <select 
                className={classes.crypto_select} 
                value={props.cryptoCoin} 
                onChange={(e) => props.onUpdateSelect(e, 'crypto')} 
                name="crypto" 
                id="crypto">
                {props.optionsCrypto}
            </select>
        </> 
    )
}