import useHttp from '../hooks/http.hook';

const useCryptoService = () => {
    const {request} = useHttp();

    //url, method = 'GET', body = null, headers = {'Content-Type': 'application/json'}

    const getCurrency = async (amount) => {
        const result = await request(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=${amount}&page=1&sparkline=false`, 
        'GET', null, {
                'accept': 'application/json',
            }
        );
        return result;
    }

    const getCrypto = async (cryptoId) => {
        const result = await request(`https://api.coingecko.com/api/v3/coins/${cryptoId}?community_data=false&developer_data=false&sparkline=false`, 
            'GET', null, {
                'accept': 'application/json',
            }
        );
        const res = _transformCrypto(result);
        return res;
    }

    const _transformCrypto = (crypto) => {
        return {
            id: crypto.id,
            symbol: crypto.symbol,
            descr: crypto.description.en,
            price: crypto.market_data.current_price.usd,
            ath_price: crypto.market_data.ath.usd,
            ath_date: crypto.market_data.ath_date.usd,
            price_change_percentage_24h: crypto.market_data.price_change_percentage_24h,
            name: crypto.name,
            image: crypto.image,
            links: crypto.links.homepage,
            market_cap: crypto.market_data.market_cap.usd,
            market_cap_rank: crypto.market_cap_rank,
            markets: crypto.tickers.map(market => [
                market.market.name, 
                market.target,
                market.last,
                market.volume,
                market.trade_url
            ])
        }
    }

    const getFiats = async () => {
        const result = await request('https://api.coingecko.com/api/v3/simple/supported_vs_currencies', 
            'GET', null, {
                'accept': 'application/json',
            }
        );
        return result;
    }

    const postAsset = (data) => {
        // request('http://localhost:3000/data', 
        //     'POST', JSON.stringify(data)
        // );
    }

    const getFiatToCrypto = async (cryptoCoin, fiatCoin) => {
        const result = await request(`https://api.coingecko.com/api/v3/simple/price?ids=${cryptoCoin}&vs_currencies=${fiatCoin}`,
            'GET', null, {
                'accept': 'application/json',
            }
        );
        return result[cryptoCoin][fiatCoin];
    }

    const getSearchList = async (query) => {
        const result = await request(`https://api.coingecko.com/api/v3/search?query=${query}`, 
            'GET', null, {
            'accept': 'application/json',
            }
        );
        return result.coins;
    }

    return {getCurrency, getCrypto, getFiats, getFiatToCrypto, getSearchList, postAsset};
};

export default useCryptoService;