import { useEffect, useState, useContext } from "react";
import { v4 as uuidv4 } from 'uuid';

import useCryptoService from '../../services/cryptoService'
import { FiatCurrency, CryptoCurrency } from "../features/Inputs";
import classes from '../../styles/app.module.css';
import context from "../context/context";

const Converter = (props) => {

    const {getFiats, getFiatToCrypto, getCurrency} = useCryptoService();

    const [fiats, setFiats] = useState([]);
    const [cryptoList, setCryptoList] = useState([]);
    const [cryptoCoin, setCryptoCoin] = useState('bitcoin');
    const [fiatCoin, setFiatCoin] = useState('usd');
    const [fiatCoinValue, setFiatCoinValue] = useState(null);
    const [inputValue, setInputValue] = useState('');
    const [converted, setConverted] = useState(0);
    const [order, setOrder] = useState(['fiat', 'crypto']);
    
    const {data} = useContext(context);

    const optionsCrypto = props.id 
                        ? <option 
                            key={uuidv4()} 
                            className={classes.options}
                            value={props.id}>{props.id.toUpperCase()}</option>
                        : cryptoList.map(coin => {
                            return (
                                <option 
                                    key={uuidv4()} 
                                    className={classes.options}
                                    value={coin.id}>{coin.name}</option>
                            )
    })

    const optionsFiat = fiats.map(fiat => {
        return (
            <option 
                key={uuidv4()} 
                value={fiat}>{fiat.toUpperCase()}</option>
        )
    })

    useEffect(() => {
        getFiats()
        .then(data => setFiats(data))
        .catch(err => console.log(err))
        // eslint-disable-next-line
    }, [])

    useEffect(() => {
        getCurrency(250)
        .then(data => setCryptoList(data))
        .catch(err => console.log(err))
        // eslint-disable-next-line
    }, [])

    const updatePrice = () => {
        getFiatToCrypto(cryptoCoin, fiatCoin)
            .then(data => setFiatCoinValue(data))
            .then(() => {
                if (order[0] === 'fiat') {
                    setConverted(inputValue / fiatCoinValue)
                } else {
                    setConverted(inputValue * fiatCoinValue)
                }
            })
    }

    const onUpdateSelect = (e, currency) => {
        if (currency === 'fiat') {
            setFiatCoin(e.target.value)
        } else {
            setCryptoCoin(e.target.value)
        }
        updatePrice();
    }

    useEffect(() => {
        updatePrice();
        // eslint-disable-next-line
    }, [fiatCoin, cryptoCoin, fiatCoinValue, props.id])

    useEffect(() => {
        if (props.id) {
            setCryptoCoin(props.id)
        } else {
            
        }
    }, [props.id])
    
    const onChangeOrder = () => {
        // const cryptoPrice = data.filter(coin => coin.id === cryptoCoin)[0].current_price;
        if (order[0] === 'crypto') {
            setOrder(['fiat', 'crypto']);
            setConverted(inputValue / fiatCoinValue)
        } else {
            setOrder(['crypto', 'fiat']);
            setConverted(fiatCoinValue * inputValue)
        }
    }
    // eslint-disable-next-line
    const inputs = order.map(item => {
        if (item === 'crypto') {
            return (
                <CryptoCurrency
                    key={uuidv4()}
                    cryptoCoin={cryptoCoin} 
                    onUpdateSelect={onUpdateSelect} 
                    optionsCrypto={optionsCrypto}/>
            )
        }
        if (item === 'fiat') {
            return (
                <FiatCurrency 
                    key={uuidv4()}
                    fiatCoin={fiatCoin} 
                    onUpdateSelect={onUpdateSelect} 
                    optionsFiat={optionsFiat}/>
            ) 
        }
    })

    const showCoin = () => {
        if (order[0] === 'fiat') {
            return fiatCoin.toUpperCase()
        } else {
            return data.filter(item => item.id === cryptoCoin)[0]?.symbol.toUpperCase() || cryptoCoin.toUpperCase()
        }
    }

    const checkLength = () => {
        if (data.length !== 0) {
            return data.filter(coin => coin.id === cryptoCoin)[0]?.symbol.toUpperCase() || cryptoCoin.toUpperCase();
        } else {
            cryptoCoin.toUpperCase();
        }
    }

    const onHandleChange = (e) => {
        // const cryptoPrice = data.filter(coin => coin.id === cryptoCoin)[0].current_price;
        setInputValue(e.target.value);
        if (order[0] === 'fiat') {
            setConverted(e.target.value / fiatCoinValue)
        } else {
            setConverted(fiatCoinValue * e.target.value)
        }
    }
    
    return (
        <div className={classes.converter}>
            <label htmlFor="converter">{order[0].charAt(0).toUpperCase() + order[0].slice(1)} to {order[1].charAt(0).toUpperCase() + order[1].slice(1)} converter</label>
            <input 
                value={inputValue}
                onChange={(e) => onHandleChange(e)}
                className={classes.crypto_input}
                placeholder='Enter amount'
                type="number" 
                name='converter' 
                id='converter'/>
            {inputs}
            <div className={classes.reverse_wrapper}>
                <span className={classes.current_price}>{checkLength()} price: {fiatCoinValue} {fiatCoin.toUpperCase()}</span>
                <button onClick={onChangeOrder} className={classes.reverse_btn}>Reverse</button>
            </div>
            <h3>You will have: <br/>
                {!(isNaN(converted)) && converted !== Infinity ? converted.toFixed(7) : '0.0000000'} {order[0] === 'fiat' ? checkLength() : fiatCoin.toUpperCase()} 
                <br/> converting {inputValue ? inputValue : 0} { showCoin() }</h3>
        </div>
    )
}

export default Converter;