import {useDispatch, useSelector} from 'react-redux';
import {v4 as uuidv4} from 'uuid';

import {sortAssets} from '../pages/portfolioSlice';
import PortfolioListItem from '../portfolioListItem/PortfolioListItem';

import classes from '../../styles/app.module.css';

const PortfolioList = () => {
    
    const { cryptoList, portfolioPrices } = useSelector(state => state.portfolio);
    const { isLogged } = useSelector(state => state.pseudoAuth);

    const coins = cryptoList.map((coin, i) => {
        return(
            <PortfolioListItem 
                prices={portfolioPrices}
                coin={coin}  
                key={uuidv4()}/>
        )
    });

    return (
        <div className={classes.portfolio__table}>
            {cryptoList.length !== 0 && isLogged ? <PortfolioTable/> : null}
            {isLogged ? coins : null}
        </div>
    );
};

const PortfolioTable = () => {
    const dispatch = useDispatch();
    return (
        <div className={classes.portfolio__table_header}>
            <span onClick={() => dispatch(sortAssets('name'))}>Coin name</span>
            <span onClick={() => dispatch(sortAssets('total'))}>Total spent</span>
            <span onClick={() => dispatch(sortAssets('holdings'))}>Holdings</span>
            <span>Avg. buy price</span>
            <span>Profit/Loss</span>
            <span>Actions</span>
        </div>
    ) 
}

export default PortfolioList;