import { Link } from 'react-router-dom';
import { useState } from 'react';
import { useDispatch } from 'react-redux';

import {deleteCryptoAsset, fetchCryptoById, showAddForm} from '../pages/portfolioSlice';
import plusImg from '../../media/plus.png'
import binImg from '../../media/bin.png'
import classes from '../../styles/app.module.css';

const PortfolioListItem = (props) => {
    const [toggleAction, setToggleAction] = useState(false);
    const { coin, prices } = props;
    const crypto = prices.find(i => i.id === coin.id);
    const profitOrLoss = isNaN((crypto?.usd * coin?.amount) - (coin?.price * coin?.amount)) ? 0.00 : (crypto?.usd * coin?.amount) - (coin?.price * coin?.amount);
    const profitOrLossPercentage = isNaN(((crypto?.usd * coin?.amount) * 100 / coin.total) - 100) ? 0.00 : (((crypto?.usd * coin?.amount) * 100 / coin.total) - 100);

    const chopCoinPrice = () => {
        if (coin.price > 500) {
            return coin.price.toFixed(2)
        } else if (coin.price > 0.01) {
            return coin.price.toFixed(4);
        } else if (coin.price > 0.0001) {
            return coin.price.toFixed(6);
        } else {
            return coin.price.toFixed(8);
        }
    }

    const colorizePrice = (price) => {
        if (price > 0) {
            return '#06c923';
        } else if (price < 0) {
            return 'red';
        } else {
            return '';
        }
    }
    
    return (
        <li className={classes.portfolio__table_item}>
            <Link 
                to={`/${coin.id}`}
                style={{textDecoration: 'none', color: 'black'}}>
                    <span>{coin.name}</span>
            </Link>
            <Link
                to={`/portfolio/${coin.id}`} 
                className={classes.portfolio__table_link}
                style={{color: 'inherit', textDecoration: 'none', display: 'flex'}}
                >
                <span data-total-spent>{coin.total.toFixed(2)} $</span>
                <span data-holdings>
                    <span style={{display: 'block'}}>{coin.amount} {coin.symbol.charAt(0).toUpperCase() + coin.symbol.slice(1)}</span>
                    <span style={{display: 'block'}}>{((crypto?.usd || 0) * coin.amount).toFixed(2)} $</span>
                </span> 
                <span data-average-price>{chopCoinPrice()}
                </span>
                <span data-profit-loss>
                    <span style={{color: `${colorizePrice(profitOrLoss)}`, display: 'block'}}>{(profitOrLoss?.toFixed(2) || 0) + ' $'}</span>
                    <span style={{
                        color: `${colorizePrice(profitOrLossPercentage)}`, 
                        display: 'block'}}>{profitOrLossPercentage > 0 
                                                ? '+' + profitOrLossPercentage.toFixed(2) 
                                                : (profitOrLossPercentage?.toFixed(2) || 0)} %</span>
                </span>
            </Link>
            <span 
                className={classes.portfolio__actions}
                onClick={() => setToggleAction(toggleAction => !toggleAction)}>...</span>
            {toggleAction ? <PortfolioActions coin={coin} /> : null}
        </li>
    );
};

const PortfolioActions = (props) => {
    const dispatch = useDispatch();
    return (
        <div className={classes.portfolio__actions_menu}>
            <span>
                <img 
                    src={plusImg} 
                    alt="add"
                    onClick={() => {
                        dispatch(fetchCryptoById(props.coin.id))
                        dispatch(showAddForm())
                    }}/>
            </span>
            <span>
                <img 
                    src={binImg} 
                    alt="remove"
                    onClick={() => dispatch(deleteCryptoAsset(props.coin.id))}/>
                </span>
        </div>
    )
}

export default PortfolioListItem;