import { useContext, useState, useEffect } from 'react';

import context from '../context/context';
import classes from '../../styles/app.module.css';

const Filters = () => {
    const {data, sortData} = useContext(context);
    const [ifSorted, setIfSorted] = useState(0);

    useEffect(() => {
        switch(ifSorted) {
            case 1:
                sortByMarketCap();
                break;
            case 2:
                sortByPriceInc();
                break;
            case 3:
                sortByPriceDec();
                break;
            default:
                break;
        }
        // eslint-disable-next-line
    }, [ifSorted])

    const sortByMarketCap = () => {
        const newData = JSON.parse(JSON.stringify(data));
        sortData(newData.sort((a, b) => b.market_cap - a.market_cap));
    }

    const sortByPriceInc = () => {
        const newData = JSON.parse(JSON.stringify(data));
        sortData(newData.sort((a, b) => b.price_change_percentage_24h - a.price_change_percentage_24h));
    }

    const sortByPriceDec = () => {
        const newData = JSON.parse(JSON.stringify(data));
        sortData(newData.sort((a, b) => a.price_change_percentage_24h - b.price_change_percentage_24h));
    }

    return (
        <div className={classes.filters}>
            <button 
                className={classes.btn}
                onClick={() => {
                    sortByMarketCap();
                    setIfSorted(1);
                }}
                >By market cap</button>
            <button 
                className={classes.btn}
                onClick={() => {
                    sortByPriceInc();
                    setIfSorted(2);
                }}
                >By price increase in 24 hours</button>
            <button 
                className={classes.btn}
                onClick={() => {
                    sortByPriceDec();
                    setIfSorted(3);
                }}
                >By price decrease in 24 hours</button>
        </div>
    );
};

export default Filters;