import classes from '../../styles/app.module.css'

const CryptoDescr = (props) => {
    const crypto = props.crypto;
    
    const changeStr = () => {
        return {__html: `${crypto.descr}`};
    }
    
    return (
        <div className={classes.crypto__descr}>
            <div className={classes.crypto_header}>
                <h2>{crypto.name}</h2>
                <img src={crypto.image?.small} alt={crypto.id} />
            </div>
            <p dangerouslySetInnerHTML={changeStr()}/>
        </div>
    );
};

export default CryptoDescr;