import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    displayModal: 'none',
    loginModal: false,
    isLogged: false,
    loginName: '',
    message: false,
    authDataEmpty: "We don't know ! :)"
}

const authSlice = createSlice({
    name: 'pseudoAuth',
    initialState,
    reducers: {
        showModal: state => {state.displayModal = 'block'},
        hideModal: state => {state.displayModal = 'none'},
        setIsLoginModal: (state, action) => {state.loginModal = action.payload},
        setLogin: (state, action) => {state.loginName = action.payload},
        userLogged: (state, action) => {state.isLogged = action.payload},
        showMessage: (state, action) => {state.message = action.payload},
        changeAuthDataStatus: (state, action) => {state.authDataEmpty = action.payload},
    }
})

const {actions, reducer} = authSlice;

export default reducer;

export const {
    showModal,
    hideModal,
    setIsLoginModal,
    setLogin,
    userLogged,
    showMessage,
    changeAuthDataStatus,
} = actions