import {Link} from 'react-router-dom';

import classes from '../../styles/app.module.css';

const SearchItem = (props) => {
    const {coin, i, isMainPage} = props;
    return (
        <div style={{display: 'flex', width: '100%'}}>
            <Link 
                style={{
                    flex: 6, 
                    borderTopRightRadius: 0, 
                    borderBottomRightRadius: 0
                }}
                to={`/${coin.id}`}
                className={`${classes.coin} ${classes.search_coin}`}>
                <div className={classes.coin_order}>
                    <span className={classes.coin_order__num}>{i + 1}</span>
                </div>
                <div className={classes.coin_wrapper}>
                    <div className={classes.coin_header} style={{justifyContent: 'space-between'}}>
                        <div style={{
                                display: 'flex', 
                                alignItems: 'center'
                            }}>
                            <h3>{coin.name}</h3>
                            <div className={classes.coin_img__wrapper}>
                                <img 
                                    style={{transform: 'translateY(0)'}} 
                                    src={coin.thumb} 
                                    alt={coin.name} />
                            </div>
                        </div>
                    </div>
                </div>
            </Link>
            <button 
                style={{
                    flex: 1, 
                    padding: '0 10px', 
                    height: '31px', 
                    marginTop: 5, 
                    borderTopLeftRadius:0, 
                    borderBottomLeftRadius: 0
                }}
                onClick={() => props.onSelectCrypto(coin.id)}
                className={classes.btn}>{isMainPage ? 'To converter' : 'Add'}</button>
        </div>
        
    );
};

export default SearchItem;