import { useState, useEffect } from 'react';

import useCryptoService from "../../services/cryptoService";
import Cryptolist from '../cryptoList/Cryptolist';
import Converter from '../converter/Converter';
import Calculator from '../calculator/Calculator';
import ErrorBoundary from '../errorBoundary/errorBoundary';  
import Footer from '../footer/Footer';  

import context from '../context/context'
import classes from '../../styles/app.module.css'
import Select from '../select/Select';
import Header from '../header/Header';
import UserModal from '../userModal/UserModal';

const MainPage = () => {
    
    const { getCurrency } = useCryptoService();
    const [data, setData] = useState([]);
    // eslint-disable-next-line
    const [cryptoId, setCryptoId] = useState(null);
    const [selectValue, setSelectValue] = useState('none');
    const [showComponent, setShowComponent] = useState([]);
    const [amount, setAmount] = useState(10);
    // const [intervalId, setIntervalId] = useState(0);
    

    const onSelectCrypto = (id) => {
        setCryptoId(id);
    }

    const onChangeAmount = (amount) => {
        setAmount(amount);
    }

    const sortData = (data) => {
        setData(data);
    }

    const updateList = () => {
        // console.log('update');
        getCurrency(amount)
        .then(data => sortData(data))
        .catch(err => console.log(err))
    }

    // useEffect(() => {
    //     if (cryptoId) {
    //         const cryptoSymbol = data.filter(item => item.id === cryptoId)[0].symbol;
    //         const cryptoName = data.filter(item => item.id === cryptoId)[0].name; 
    //         props.onSetCrypto([cryptoName, cryptoSymbol]);
    //     }
    //     // eslint-disable-next-line
    // }, [cryptoId])

    useEffect(() => {
        // console.log('effect');
        updateList();

        // if (intervalId) {
        //     clearInterval(intervalId)
        // }
        
        // // const interval = setInterval(updateList, 5000);
        // // setIntervalId(interval);

        // return () => clearInterval(intervalId);
        // eslint-disable-next-line
    }, [amount]);

    const { Provider } = context;

    useEffect(() => {
        if (selectValue !== 'none') {
            if (selectValue === 'Cryptolist') {
                setShowComponent(['flex', 'none'])
            } else {
                setShowComponent(['none', 'flex'])
            }}
    }, [selectValue])
    
    return (
        <Provider value={{data, sortData}}>
            <Header />
            <UserModal />
            <div className={classes.main}>
                <section>
                    <div style={{
                                display: `${showComponent[0] }`, 
                                flexDirection: 'column'
                            }}>
                            <div className={classes.select_container} >
                                <Select 
                                // style={{display: `${selectValue === 'Calculators' ? 'none' : 'flex'}`}}
                                // style={{display: 'none'}}
                                selectValue={selectValue}
                                setSelectValue={setSelectValue}
                                />
                            </div>
                        
                        <ErrorBoundary>
                            <Cryptolist 
                                data={data} 
                                onSelectCrypto={onSelectCrypto} 
                                onChangeAmount={onChangeAmount}
                                sortData={sortData}
                                amount={amount}/>
                        </ErrorBoundary>
                    </div>
                    
                </section>
                <section>
                    <div 
                        className={classes.features_container} 
                        style={{display: `${ showComponent[1] }`}}>
                            <Select
                                style={{display: `${selectValue === 'Calculators' ? 'flex' : 'none'}`}}
                                selectValue={selectValue}
                                setSelectValue={setSelectValue}
                            />
                            <ErrorBoundary>
                                <Converter 
                                    id={cryptoId}
                                    sortData={sortData}/>
                            </ErrorBoundary>
                            <ErrorBoundary>
                                <Calculator/>
                            </ErrorBoundary>       
                    </div>
                </section>
            </div>
            <Footer/>
        </Provider>
    );
};

export default MainPage;