import { useRef } from "react";
import { useEffect, useState } from "react";
import { v4 as uuidv4 } from 'uuid';
import classes from '../../styles/app.module.css';

const Calculator = () => {
    const [inputs, setInputs] = useState(3);
    const [inputElements, setInputElements] = useState([])
    const [price, setPrice] = useState(null);
    const [amount, setAmount] = useState(null);
    const [total, setTotal] = useState(null);
    const [value, setValue] = useState({});

    useEffect(() => {
        const generateInputs = (type) => {
            const arr = new Array(inputs);
            arr.fill('1');
            return arr.map((_, i) => {
                const id = `${type}${i + 1}`
                return (
                    <input 
                        key={uuidv4()}
                        ref={el => {
                            if (type === 'average') {
                                itemRefs.current[i] = el
                            } else {
                                itemRefs1.current[i] = el
                            }
                        }}
                        name={id}
                        onChange={(e) => {
                            let str = e.target.value.replace(/\D/g, '')
                            setValue(value => ({...value, [id]: str}))
                            
                        }}
                        value={value.id}
                        type="number"/>
                );
            })
        }
        setInputElements([generateInputs('average'), generateInputs('amount')]);
        // eslint-disable-next-line
    }, [inputs])

    const getResult = (amount, total) => {
        const arrOfType = Object.entries(value).filter(item => item[0].includes('average'));
        const arrOfNonType = Object.entries(value).filter(item => !(item[0].includes('average')));
        const priceArr = arrOfType.map((item, i) => { 
            return +item[1] * (arrOfNonType.length >= i + 1 ? +arrOfNonType[i][1] : 1);
        })

        const sumOfCoins = arrOfNonType.map(item => +item[1]).reduce((a, b) => a + +b, 0);
        const sumOfPrices = priceArr.reduce((a, b) => a + +b, 0);
        if (amount) return sumOfCoins;
        if (total) return sumOfPrices;
        return isNaN((sumOfPrices / sumOfCoins)) || (sumOfPrices / sumOfCoins) === Infinity ? null : (sumOfPrices / sumOfCoins).toFixed(5);
    } 

    const itemRefs = useRef([]);
    const itemRefs1 = useRef([]);

    useEffect(() => {
        setPrice(getResult())
        setAmount(getResult(true))
        setTotal(getResult(false, true))
        // console.log(Object.values(value).map(i => i = 5));
        // console.log(value);
        // console.log(itemRefs.current[0].name);
        itemRefs.current.forEach(item => item && value[item.name] ? item.value = value[item.name].replace(/\D/g, '') : '');
        itemRefs1.current.forEach(item => item && value[item.name] ? item.value = value[item.name].replace(/\D/g, '') : '');
        // eslint-disable-next-line
    }, [value])

  

    const clearInputs = () => {
        const newValue = JSON.parse(JSON.stringify(value));
        for(let key in value) {
            newValue[key] = '';
        }
        itemRefs.current.forEach(item => item ? item.value = '' : '');
        itemRefs1.current.forEach(item => item ? item.value = '' : '');
        setValue(newValue);
    }

    return (
        <div className={classes.converter}>
            <h3 className={classes.calculator_header}>Average price calculator</h3>
            <div className={classes.average_container}>
                <div className={classes.average_container__inner}>
                    <h3>Crypto price:</h3>
                    {inputElements[0]}
                </div>
                <div className={classes.average_container__inner}>
                    <h3>Amount of crypto:</h3>
                    {inputElements[1]}
                </div>
            </div>
            <h3 style={{marginRight: '10px'}}>Average price: {price ? price : '________'}</h3>
            <h3>Amount of crypto: {amount ? amount : '________'}</h3>
            <h3>Total spend: {total ? total : '________'}</h3>
            <div className={classes.btn_container}>
                <button 
                    className={classes.btn} 
                    onClick={() => setInputs(inputs => inputs + 1)}
                    >Add one input</button>
                <button 
                    className={classes.btn} 
                    onClick={() => setInputs(inputs => inputs - 1)}
                    >Delete one input</button>
                <button 
                    className={classes.btn} 
                    onClick={clearInputs}
                    >Reset</button>
            </div>
        </div>
    )
}

export default Calculator;