import {v4 as uuidv4} from 'uuid';

import classes from '../../styles/app.module.css';

const CryptoData = (props) => {
    const {crypto} = props;
    
    const someStyle = (price) => {
        if (price < 0) {
            return {color: 'red'}
        }
    }  

    const showSpaces = (numb) => {
        return [...(numb + '')].reverse().join('').replace(/([0-9]{3})/g, '$1 ').split('').reverse().join('');
    } 

    const links = crypto.links?.map(link => {
        if (link === '') return null;
        return (
            <li key={uuidv4()}>
                <a href={link}>{link}</a>
            </li>
        )
    })

    return (
        <div className={classes.crypto_data}>
                <div>
                    <h3>Links:</h3>
                    <ul>
                        {links}
                    </ul>
                </div>
                <div className={classes.market_data}>
                    <h4><span>Current price:</span><span>{crypto.price} $</span></h4>
                    <h4><div><span className={classes.ath}><p className={classes.ath_hint}>All-time hight</p>ATH</span> price:</div><span>{crypto.ath_price} $</span></h4>
                    <h4><span>ATH date:</span><span>{crypto?.ath_date?.slice(0, 10)}</span></h4>
                    <h4><span>Market cap:</span><span>{showSpaces(crypto.market_cap || '')} $</span></h4>
                    <h4><span>Market cap rank:</span><span>{crypto.market_cap_rank}</span></h4>
                    <h4><span>24 hours price change in percentage:</span><span style={someStyle(crypto.price_change_percentage_24h)}>{crypto.price_change_percentage_24h} %</span></h4>
                </div>
            </div>
    );
};

export default CryptoData;