import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import CryptoJs from 'crypto-js';

import {
    hideModal, 
    setLogin, 
    userLogged, 
    setIsLoginModal, 
    changeAuthDataStatus} from '../../slices/authSlice';

import classes from '../../styles/app.module.css';

const schemaSignIn = yup.object({
    login: yup.string().required().min(3),
    password: yup.string()
        .required('No password provided.') 
        .min(8, 'Password is too short! It should be 8 chars minimum.')
        .matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/, 'Password should contain at least one digit, one lower and one upper case letter and contain at least 8 from the mentioned characters'),
  }).required();

const schemaSignUp = yup.object({
    login: yup.string().required().min(3),
    email: yup.string().email(),
    password: yup.string()
        .required('No password provided.') 
        .min(8, 'Password is too short! It should be 8 chars minimum.')
        .matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}$/, 'Password should contain at least one digit, one lower and one upper case letter and contain at least 8 from the mentioned characters'),
    passwordRepeat: yup.string().oneOf([yup.ref('password'), null], 'Password must match')
}).required();

const UserModal = () => {
    const {loginModal} = useSelector(state => state.pseudoAuth);

    return (
        <>
            {loginModal ? <SignIn/> : <SignUp/>}
        </>
    );
};

const SignIn = () => {
    const [authFail, setAuthFail] = useState(false);
    // const [authDataEmpty, setAuthDataEmpty] = useState("We don't know ! :)");
    const dispatch = useDispatch();
    const {displayModal, authDataEmpty} = useSelector(state => state.pseudoAuth);

    const { register, handleSubmit, reset, formState, formState: { errors }} = useForm({
        resolver: yupResolver(schemaSignIn),
        defaultValues: {
            login: '',
            password: ''
        }
    });

    const onSubmit = data => {
        if (localStorage.auth) {
            const authData = localStorage.auth;
            const bytes = CryptoJs.AES.decrypt(authData, 'bitcoin')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            if (decrData.login === data.login && decrData.password === data.password) {
                setAuthFail(false);
                dispatch(changeAuthDataStatus(false))
                dispatch(hideModal());
                dispatch(userLogged(true))
                dispatch(setLogin(data.login))

                //show success message
                
            } else {
                setAuthFail(true);
            } 
        } else {
            dispatch(changeAuthDataStatus(true))
        }
    };

    useEffect(() => {
        if (formState.isSubmitSuccessful) {
            reset({
                login: '',
                password: ''
            });
        } 
    }, [formState, reset])

    return (
        <>
            <div 
                className={classes.user_modal}
                style={{display: `${displayModal}`}}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <h1>Crypto.features</h1>
                    <h6>Sign in</h6>

                    <input 
                        {...register('login')} 
                        placeholder="Enter your login"
                        type="text"
                        />
                    <p>{errors.login?.message}</p>
                    
                    <input 
                        {...register('password')}  
                        type="password" 
                        placeholder="Enter your password"/>
                    <p>{errors.password?.message}</p>
                    <p style={{
                        display: `${authFail ? 'block' : 'none'}`, 
                        textAlign: 'center', 
                        color: 'red', 
                        marginTop: 10}}
                        >You entered wrong login or password. </p>
                    <p style={{
                        display: `${authDataEmpty === false ? 'none' : 'block'}`, 
                        textAlign: 'center', 
                        color: 'red', 
                        marginTop: 10}}
                        >Please register first</p>
                    

                    {authDataEmpty === false
                        ? <button className={classes.btn}>Log in</button> 
                        : <button 
                            className={classes.btn}
                            onClick={() => {
                                dispatch(setIsLoginModal(false))
                            }}>To sign up form</button>}
                </form>
                <div 
                    className={classes.close_modal}
                    onClick={() => {
                        dispatch(hideModal())
                        setAuthFail(false)
                    }}
                    >x</div>
            </div>
            <div 
                className={classes.body_hover} 
                style={{display: `${displayModal}`}}></div>
        </>
    )
}

const SignUp = () => {
    const dispatch = useDispatch();
    const {displayModal} = useSelector(state => state.pseudoAuth);

    const { register, handleSubmit, reset, formState, formState: { errors }} = useForm({
        resolver: yupResolver(schemaSignUp),
        defaultValues: {
            login: '',
            email: '',
            password: '',
            passwordRepeat: ''
        }
    });

    const onSubmit = data => {
            delete data.passwordRepeat;
            const encData = CryptoJs.AES.encrypt(JSON.stringify(data), 'bitcoin').toString()
            
            localStorage.setItem('auth', encData)
            dispatch(hideModal())
            //show success message
            if (localStorage.auth) {
                const bytes = CryptoJs.AES.decrypt(encData, 'bitcoin')
                const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
                const login = (decrData.login);
                dispatch(setLogin(login))
                dispatch(userLogged(true))
            }
    };

    useEffect(() => {
        if (formState.isSubmitSuccessful) {
            reset({
                login: '',
                email: '',
                password: '',
                passwordRepeat: ''
            });
        }
    }, [formState, reset])

    return (
        <>
            <div 
                className={classes.user_modal}
                style={{display: `${displayModal}`}}>
                <form onSubmit={handleSubmit(onSubmit)}>
                    <h1>Crypto.features</h1>
                    <h6>Registration form</h6>
                    
                    <input 
                        {...register('login')} 
                        placeholder="Enter your login"
                        type="text"
                        />
                    <p>{errors.login?.message}</p>
                    
                    
                    <input 
                        {...register('email')} 
                        type="email" 
                        placeholder="Enter your email"
                        />
                    <p>{errors.email?.message}</p>
                    
                    <input 
                        {...register('password')}  
                        type="password" 
                        placeholder="Enter your password"/>
                    <p>{errors.password?.message}</p>
                    
                    
                    <input 
                        {...register('passwordRepeat')} 
                        type="password" 
                        placeholder="Repeat your password"/>
                        <p>{errors.passwordRepeat?.message}</p>

                    <button className={classes.btn}>Submit</button>
                </form>
                <div 
                    className={classes.close_modal}
                    onClick={() => dispatch(hideModal())}
                    >x</div>
            </div>
            <div 
                className={classes.body_hover} 
                style={{display: `${displayModal}`}}></div>
        </>
    )
}


export default UserModal;