import { useContext, useState } from 'react';

import useCryptoService from '../../services/cryptoService';
import context from '../context/context';
import useDebounce from "../../hooks/useDebounce";
import classes from '../../styles/app.module.css';

const Search = (props) => {
    const {data} = useContext(context);
    const [value, setValue] = useState('');
    const { getSearchList } = useCryptoService();
    const {amount, onChangeAmount, setSearchQuery, onManualSearch, isMainPage, onSetList} = props;
    const debouncedSearch = useDebounce(setSearchQuery, 500);

    function onHandleSubmit(e) {
        e.preventDefault();

        if (isMainPage) {
            getSearchList(value)
                .then(data => onManualSearch(data))
                .then(setValue(''))
                .then(debouncedSearch(data.filter((_, i) => i < 10)))
                .then(onChangeAmount(10))
                .catch(err => console.log(err))
        }
        getSearchList(value)
            .then(data => onSetList(data))
            .then(setValue(''))
            .catch(err => console.log(err))
    }

    return (
        <form 
            onSubmit={(e) => onHandleSubmit(e)}
            action="" 
            className={classes.search_form}>
            <input 
                className={classes.search} 
                value={value}
                onChange={(e) => {
                    if (isMainPage) {
                        if (amount <= 250) {
                            onChangeAmount(251);
                            debouncedSearch(data?.filter(item => item.name.toLowerCase().includes(e.target.value.toLowerCase())));
                        } else {
                            debouncedSearch(data?.filter(item => item.name.toLowerCase().includes(e.target.value.toLowerCase())));
                        }
                    }
                    setValue(e.target.value);
                }}
                type="text" 
                placeholder="Coin name"/>
            <button className={classes.btn}>Search</button>
        </form>
    );
};

export default Search;