import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from 'react-router-dom';
import {v4 as uuidv4} from 'uuid';

import { checkSavedAssets, fetchPortfolioPrices } from './portfolioSlice';
import Header from '../header/Header';
import plusImg from '../../media/plus.png'
import binImg from '../../media/bin.png'

import classes from '../../styles/app.module.css';
import { useEffect } from 'react';

const PortfolioAssetHistoryPage = () => {
    const dispatch = useDispatch();
    const [toggleAction, setToggleAction] = useState(false);
    const { cryptoId } = useParams();
    const { assetsHistory, cryptoList } = useSelector(state => state.portfolio);

    useEffect(() => {
        dispatch(checkSavedAssets())
        // eslint-disable-next-line
    }, [])

    useEffect(() => {
        dispatch(fetchPortfolioPrices(cryptoList.map(i => i.id)))
        // eslint-disable-next-line
    }, [])

    const loc = document.location.href.replace(/.+?(?=portfolio)(portfolio.)/g, '')
    const crypto = assetsHistory.filter(item => item.id === (cryptoId || loc));
    const asset = cryptoList.find(coin => coin.id === (cryptoId || loc));


    const assets = crypto.map((asset, i) => {
        return (
            <li
                key={uuidv4()}>
                {/* <span>{asset.id}</span> */}
                <span>{asset.type.charAt(0).toUpperCase() + asset.type.slice(1)}</span>
                <span>{asset.price} $</span>
                <span>{asset.amount} {asset.symbol.toUpperCase()}</span>
                <span 
                className={classes.portfolio__actions}
                onClick={() => setToggleAction(toggleAction => !toggleAction)}>...</span>
                {toggleAction ? <PortfolioActions coin={asset} /> : null}
            </li>
        )
    })
    
    return (
        <>
            <Header/>
            <section className={classes.portfolioAssets__container}>
                <div className={classes.portfolioAssets__total}>
                {/* <button 
                            className={`${classes.btn} ${classes.btn_asset}`} 
                            style={{
                                width: 100, 
                                marginBottom: 20,
                                position: 'relative',
                                left: 'calc(100vw - 225px)',
                                top: 100
                                }}>Back</button> */}
                    <h3>{crypto[0]?.id.toUpperCase()}</h3>
                    <div>
                        <h2 >{asset?.holdings ? asset?.holdings.toFixed(2) : cryptoList.filter(i => i.id === loc).reduce((a, b) => a + (b.amount * b.price), 0).toFixed(2)} $</h2>
                        <span style={{color: 'teal'}}>Total holdings</span>
                    </div>
                    <div className={classes.portfolioAssets__total_box}>
                        <span>{asset?.amount} {asset?.symbol.toUpperCase()}</span>
                        <span>{asset?.price} $</span>
                        <span>Total profit / loss</span>
                    </div>
                    <div className={classes.portfolioAssets__total_box} style={{position: 'relative'}}>
                        <span>Amount</span>
                        <span>Avg.buy price</span>
                        <span>Total profit / loss</span>
                        
                    </div>
                </div>
                <PortfolioTable/>
                <ul>
                    {assets}
                </ul>
            </section>
        </>
        
        
    );
};

const PortfolioTable = () => {
    // const dispatch = useDispatch();
    return (
        <div className={classes.portfolio__table_header}
            style={{textAlign: 'center'}}>
            <span>Type</span>
            <span style={{display: 'block'}}>Price</span>
            <span>Amount</span>
            <span>Actions</span>
        </div>
    ) 
}

const PortfolioActions = () => {
    // const dispatch = useDispatch();
    return (
        <div className={classes.portfolio__actions_menu} style={{height: 79, right: 10}}>
            <span>
                <img 
                    style={{marginTop: 25}}
                    src={plusImg} 
                    alt="add"
                    onClick={() => {
                        // dispatch(fetchCryptoById(props.coin.id))
                        // dispatch(showAddForm())
                    }}/>
            </span>
            <span>
                <img 
                    style={{marginTop: 25}}
                    src={binImg} 
                    alt="remove"
                    // onClick={() => dispatch(deleteCryptoAsset(props.coin.id))}
                    />
                </span>
        </div>
    )
}

export default PortfolioAssetHistoryPage;