import { Link } from 'react-router-dom';
import { useState, useRef, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CryptoJs from 'crypto-js';

import {
    showModal, 
    setIsLoginModal, 
    setLogin, 
    userLogged, 
    showMessage, 
    changeAuthDataStatus} from '../../slices/authSlice';

import classes from '../../styles/app.module.css';

const Header = () => {
    const [showMenu, setShowMenu] = useState(false);
    const dispatch = useDispatch();
    const { isLogged } = useSelector(state => state.pseudoAuth);
    const refMenu = useRef();

    useEffect(() => {
        if (localStorage.auth) {
            const authData = localStorage.auth;
            const bytes = CryptoJs.AES.decrypt(authData, 'bitcoin')
            const decrData = JSON.parse(bytes.toString(CryptoJs.enc.Utf8))
            const login = (decrData.login);
            dispatch(setLogin(login))
            dispatch(userLogged(true))
            dispatch(changeAuthDataStatus(false))
        }
        // eslint-disable-next-line
    }, [])

    const showAnimation = (type) => {
        if (!type) {
            let op = 0;
            const id = setInterval(frame, 5);
            function frame() {
                if (op >= 1) {
                    clearInterval(id);
                } else {
                    refMenu.current.parentElement.style.display = 'block';
                    op += 0.02;
                    refMenu.current.style.opacity = op;
                }
            }
        } else {
            let op = 1;
            const id = setInterval(frame, 5);
            function frame() {
                if (op <= 0) {
                    clearInterval(id);
                    refMenu.current.style.opacity = 0;
                    refMenu.current.parentElement.style.display = 'none';
                } else {
                    op -= 0.02;
                    refMenu.current.style.opacity = op;
                }
            }
        }
    }

    return (
        <header>
            <div className={classes.header__wrapper}>
                <h1><Link to="/">Crypto.features</Link></h1>
                <nav>
                    <ul>
                        {/* eslint-disable-next-line */}
                        <li><Link 
                            to="/">Mainpage</Link></li>
                        {/* eslint-disable-next-line */}
                        <li><Link 
                            to="/portfolio">Portfolio</Link></li>
                        {isLogged ? <UserLogged/> : <UserAuthentication />}
                    </ul>
                    <span 
                        onClick={() => {
                            showAnimation(showMenu);
                            setShowMenu(showMenu => !showMenu)
                        }}
                        className={`${classes.header__hamburger} ${showMenu ? classes.header__hamburger_modified : null}`}>
                        <span></span>
                        <span></span>
                        <span></span>
                    </span>
                    <div 
                        style={{display: 'none'}}
                        className={classes.header__menu}>
                        <ul ref={refMenu}>
                            {/* eslint-disable-next-line */}
                            <li><Link 
                                to="/">Mainpage</Link></li>
                            {/* eslint-disable-next-line */}
                            <li><Link 
                                to="/portfolio">Portfolio</Link></li>
                            {isLogged ? <UserLogged/> : <UserAuthentication />}
                        </ul>
                    </div>
                </nav>
            </div>
        </header>
    );
};

const UserAuthentication = () => {
    const dispatch = useDispatch();

    const onBtnClick = (payload) => {
        dispatch(showModal())
        dispatch(setIsLoginModal(payload))
        dispatch(showMessage(false))
    } 

    return (
        <>
            {/* eslint-disable-next-line */}
            <li><a  href="#"
                    onClick={() => onBtnClick(true)}                                
                    >Log in</a></li>
            {/* eslint-disable-next-line */}
            <li><a  href="#"
                    onClick={() => onBtnClick(false)}  
                    >Register</a></li>
        </>
    )
}

const UserLogged = () => {
    const dispatch = useDispatch();
    const { loginName } = useSelector(state => state.pseudoAuth);

    const onLogOut = () => {
        // localStorage.removeItem('auth');
        dispatch(setLogin(''))
        dispatch(userLogged(false));
    }

    return (
        <>
            <li style={{padding: '0 10px', color: 'wheat'}}>{loginName}</li>

            <li 
                style={{padding: '0 10px', color: 'white'}} 
                onClick={onLogOut}>Log out</li>
        </>
    )
}

export default Header;