import { useState } from 'react';
import {useDispatch, useSelector} from 'react-redux';

import {hideAddForm, modifyCrypto} from '../../slices/portfolioSlice';

import classes from '../../styles/app.module.css';

const PortfolioItemAddForm = (props) => {
    const [transactionType, setTransctionType] = useState('buy');
    const [amount, setAmount] = useState('');
    const [price, setPrice] = useState('');
    const dispatch = useDispatch();
    const selectedCrypto = useSelector(state => state.portfolio.selectedCrypto);
    const amountError = useSelector(state => state.portfolio.amountError);

    return (
        <div 
            className={classes.portfolio__form}
            style={props.style}>
            <div 
                className={classes.close_modal}
                onClick={() => dispatch(hideAddForm())}
                >x</div>
            <h4>Add transaction</h4>
            <form>
                <input 
                    type='radio' 
                    name='transactionType' 
                    id='tr-buy' 
                    className={classes.portfolio__form_type} 
                    value='buy'
                    checked={transactionType === 'buy' ? 'checked' : ''}
                    onChange={(e) => setTransctionType(e.target.value)}/>
                <label htmlFor="tr-buy">Buy</label>
                <input 
                    type='radio' 
                    name='transactionType' 
                    id='tr-sell' 
                    className={classes.portfolio__form_type} 
                    value='sell' 
                    checked={transactionType === 'sell' ? 'checked' : ''}
                    onChange={(e) => setTransctionType(e.target.value)}/>
                <label htmlFor="tr-sell">Sell</label>
            </form>
            <h4 style={{color: 'teal'}}>{selectedCrypto?.name}</h4>
            <div className={classes.portfolio__form_container}>
                <div>
                    <label htmlFor="quantity">Quantity</label>
                    <input 
                        type="number" 
                        name='quantity'
                        className={classes.portfolio_input} 
                        value={amount}
                        onChange={(e) => setAmount(e.target.value)}
                        placeholder='0.00'/>
                </div>
                <div>
                    <label htmlFor="price">$ Price Per Coin</label>
                    <input 
                        type="number" 
                        name='price' 
                        className={classes.portfolio_input} 
                        value={price}
                        onChange={(e) => setPrice(e.target.value)}
                        placeholder={selectedCrypto?.current_price}/>
                </div>
            </div>
            <div className={classes.portfolio__form_result}>
                <span>TOTAL SPENT</span>
                <p>{isNaN(+price || selectedCrypto?.current_price * +amount) 
                        ? '' 
                        : price === '' 
                                ? (selectedCrypto?.current_price * +amount).toFixed(4) 
                                : (price * amount).toFixed(4)}</p>
            </div>
            <h3 
                style={{
                    display: `${amountError ? 'block' : 'none'}`,
                    width: '85%', 
                    margin: '0 auto 20px auto', 
                    color: 'red'
                }}>Sorry, You don't have enough amount of coins!</h3>
            <button 
                onClick={() => {
                    const data = {
                        type: transactionType,
                        id: selectedCrypto?.id,
                        name: selectedCrypto?.name,
                        symbol: selectedCrypto.symbol,
                        amount: +amount,
                        price: +price || +selectedCrypto?.current_price,
                        total: (price === '' ? selectedCrypto?.current_price * +amount : price * amount),
                        price_change_percentage_24h: selectedCrypto?.price_change_percentage_24h
                    }
                    dispatch(modifyCrypto(data));
                    setAmount('');
                    setPrice('');
                }}
                className={classes.btn}>Add transaction</button>
        </div>
    );
};

export default PortfolioItemAddForm;